import axiosInstance from './axiosInstance'; // Importujemy instancjÄ™ Axios

// Pobieranie wszystkich pojemnikÃ³w
export const getContainers = async () => {
  try {
    const response = await axiosInstance.get('containers/');
    return response.data; // Zwracamy dane po pobraniu
  } catch (error) {
    console.error('BÅ‚Ä…d przy pobieraniu pojemnikÃ³w:', error);
    throw error;
  }
};

// Dodawanie nowego pojemnika
export const addContainer = async (newContainer) => {
  try {
    console.log("WysyÅ‚ane dane:", newContainer); //debug dane
    const response = await axiosInstance.post('containers/', newContainer);
    return response.data;
  } catch (error) {
    console.error('BÅ‚Ä…d przy dodawaniu pojemnika:', error);
    throw error;
  }
};

