import React, { useState, useEffect } from 'react';
import RuraSelection from '../components/RuraSelection';
import AddWeightForm from '../components/AddWeightForm';
import RecentEntries from '../components/RecentEntries';
import { addWeightEntry } from '../api/containersApi';
import axios from '../api/axiosInstance';

const HomePage = () => {
  const [materials, setMaterials] = useState([]);
  const [selectedType, setSelectedType] = useState('');
  const [selectedMaterial, setSelectedMaterial] = useState('');
  const [entries, setEntries] = useState([]);
  const [selectedSize, setSelectedSize] = useState("");

  useEffect(() => {
    // Wykonaj GET na dziaÅ‚ajÄ…cy endpoint, Å¼eby backend ustawiÅ‚ cookie csrftoken
    axios.get('/materials/')
      .then(response => {
        setMaterials(response.data);
      })
      .catch(error => {
        console.error('BÅ‚Ä…d pobierania materiaÅ‚Ã³w:', error);
      });
  }, []);

  // Pobierz ostatnie wpisy
  const fetchEntries = async () => {
    try {
      const response = await axios.get('/containers/');
      const recent = response.data.slice(-5).reverse();
      setEntries(recent);
    } catch (error) {
      console.error('BÅ‚Ä…d podczas pobierania wpisÃ³w:', error);
    }
  };

  useEffect(() => {
    fetchEntries();
  }, []);

  const handleAddWeight = async (containerData) => {
    try {
      const savedEntry = await addWeightEntry(containerData);
      setEntries([savedEntry, ...entries.slice(0, 4)]);
    } catch (error) {
      console.error('BÅ‚Ä…d przy dodawaniu wagi:', error);
      alert('Nie udaÅ‚o siÄ™ zapisaÄ‡ danych. SprawdÅº poÅ‚Ä…czenie z backendem.');
    }
  };

  return (
    <div>
      <RuraSelection
        selectedType={selectedType}
        setSelectedType={setSelectedType}
        selectedMaterial={selectedMaterial}
        setSelectedMaterial={setSelectedMaterial}
        selectedSize={selectedSize}
        setSelectedSize={setSelectedSize}
      />
      <AddWeightForm
        selectedType={selectedType}
        selectedMaterial={selectedMaterial}
        selectedSize={selectedSize}
        onSubmit={handleAddWeight}
      />
      <RecentEntries entries={entries} refreshEntries={fetchEntries} />
    </div>
  );
};

export default HomePage;
